
import wx from 'weixin-js-sdk'
import { mapGetters,mapActions }from 'vuex'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

export default {
components: {
Swipeout, SwipeoutItem, SwipeoutButton
},
data () {
return {
listObj:{
max:0,
list:[]
}
}
},
created(){
document.title = "浏览记录"
localStorage.setItem('token','Bearer eyJhbGciOiJIUzUxMiJ9.eyJzdWIiOiIxNzc1NTc1NTY3NiIsInNjb3BlcyI6WyJVU0VSIl0sImlzcyI6Imh0dHA6Ly95dWFvcS5jb20iLCJpYXQiOjE1MjY4ODcwMzksImV4cCI6MTU0NDg4NzAzOX0.JP90fTfrOmDqlVHHyTKJg8WP9TB-O-wdTXX6Xd41xkFJhYLguQf3jUVcY4cvc_d5O0nCBf3laQLtS0AOWjcl8w')
if(this.$route.query.token){
localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
}
if(this.$route.query.refreshToken){
localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
}

this.getData()
},
methods: {
...mapActions(['browseHistorys','delBrowseHistorys']),
goHome(){
wx.miniProgram.switchTab({
url:'/pages/home/index'
})
},
goTo(id){
wx.miniProgram.navigateTo({
url:'/pages/goodDetail/index?id='+ id
})
},
delJv1(){
this.delBrowseHistorys({data:{isAllClear:true},this:this})
},
delJv(id){
this.delBrowseHistorys({data:{browseHistoryId:id},this:this})
},
getCopy(ary){
this.listObj = ary
},
getData(i){
this.showLoading()
this.browseHistorys({this:this,data:{viewIndex:0,viewSize:10},method:this.browseHistorys,fn:i})
},
handleEvents (type) {
console.log('event: ', type)
}
}
}



